export class BaseInformationBean {
    pageNum: number = 1;      //			当前页号
    pageSize: number = 10;      //			每页显示数量

    surveyId: string;   //调查id
    surveyName: string; //调查名称
    description: string;    //调查描述
    comments: string;   //评论
    submitCaption: string;//提交标题
    responseService: string;//响应服务
    isAnonymous: string;//是否匿名
    allowMultiple: string;//允许多个
    allowUpdate: string;//是否更新
}