//  基本信息（调查）-查询 熊正好 2019.4
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../../../http/product-manage.http.service";
import { ConfirmationService } from 'primeng/primeng';//删除
import { BaseInformationBean } from './../../../bean/base-information.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import * as _ from 'lodash';

@Component({
    selector: 'app-base-information-suvery-increased',
    templateUrl: './base-information-suvery-increased.component.html',
    styleUrls: ['./../../base-information-suvery.component.scss'],
    providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 个性化管理-基本信息（调查）
 */
export class BaseInformationSuveryIncreasedComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;
    tabMesg: any = tableMessage;//列表无数据
    //提示信息
    msgs: Message[] = [];
    //模态框
    display: boolean = false;
    surveyIdes: string = '';
    surveyIdes1: string = '';
    options: any = [];
    surveyId2: string = '';
    description: string = '';
    QuestionLists: any = [
        { 'label': '请选择', 'value': '' },
    ];

    constructor(public productManageHttpService: ProductManageHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc) { }

    ngOnInit() {
        this.surveyIdes = this.inValue;
        this.queryQuestionType();
    }
    //属性值类型
    queryQuestionType() {
        this.productManageHttpService.queryQuestionType({}).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.QuestionLists = [
                    { 'label': '请选择', 'value': '' },
                ];
                let obj = data.questionTypeMap;
                for (var key in obj) {
                    this.QuestionLists.push({
                        label: obj[key],
                        value: key,
                    });
                }
                this.QuestionLists = _.clone(this.QuestionLists);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //取消
    addCloseBtn() {
        let parm = {
            display: false,
            code: 'cancelModel'
        }
        this.outValue.emit(parm);
    }
    //确认
    addSaveBtn() {
        let pamrem = {
            question: this.surveyId2,//属性名称
            surveyQuestionCategoryId: this.surveyIdes,//属性分类id
            questionTypeId: this.surveyIdes1,//属性值类型
            description: this.description,//属性描述
        };
        //提交服务
        this.productManageHttpService.createSurveyQuestion(pamrem).subscribe(data => {
            if (data.returnCode.code == "success") {
                let parm = {
                    display: false,
                    code: data.returnCode.message
                }
                this.outValue.emit(parm);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })

    }
}
