//  基本信息（调查）-查询 熊正好 2019.4
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../../http/product-manage.http.service";
import { ConfirmationService } from 'primeng/primeng';//删除
import { BaseInformationBean } from './../../bean/base-information.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import * as _ from 'lodash';

@Component({
    selector: 'app-base-information-suvery-add',
    templateUrl: './base-information-suvery-add.component.html',
    styleUrls: ['./../base-information-suvery.component.scss'],
    providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 个性化管理-基本信息（调查）
 */
export class BaseInformationSuveryAddComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;
    tabMesg: any = tableMessage; // 列表无数据
    // bean
    baseInformationBean: BaseInformationBean = new BaseInformationBean();
    // 修改的参数
    updateValue: any;
    options: any;
    // 提示信息
    msgs: Message[] = [];
    // 总页数
    total: any = 0;
    total1: any = 0;
    first: number = 0; // 分页控制
    first1: number = 0; // 分页控制
    pageSize1: number = 10;
    pageSize: number = 10;
    pageNum: number;
    pageNum1: number;
    // 模态框
    showModel: string;
    display: boolean = false;
    // 模态框的标题
    headerTitle: string;
    site_list: any = [];
    site_list1: any = [];
    surveyIdes: string = '';
    // 左右移动
    checkBoxAll10: boolean;
    checkBox10: any = [];
    cars_right: any = [];// 组件数据-右（未选）
    comType: any = [];// 组件状态是否为必选
    checkBox11: any = [];
    checkBoxAll11: Boolean = false;//是否全选(右)
    constructor(public productManageHttpService: ProductManageHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc) { }

    ngOnInit() {
        this.baseInformationBean = this.inValue;
        this.queryClick();
        this.queryClick1();
    }
    // 新增调查项
    addClick() {
        this.display = true;
        this.headerTitle = '新增';
        this.showModel = '1';
        this.updateValue = this.surveyIdes;
    }
    // 新增调查项
    addsCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.first = 0;
        this.queryClick();
    }
    suveryfClick(item) {

    }
    //  查询
    queryClick() {
        let pamarm = {
            surveyId: this.baseInformationBean.surveyId,
        };
        this.productManageHttpService.querySurveyProject(pamarm).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.site_list = data.questionList;
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    //  查询1
    queryClick1() {
        let pamarms = {
            surveyId: '',
            pageNum: 1,
            pageSize: 1000,
        };
        this.productManageHttpService.querySurveyProject(pamarms).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.site_list1 = data.questionList;
                this.total1 = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    //可选全选
    checkBoxAllClick10() {
        this.checkBox10 = [];
        let emptyArr = [];
        if (this.checkBoxAll10 == true) {
            for (let i = 0; i < this.site_list1.length; i++) {
                this.checkBox10.push(i.toString())
            }
        } else if (this.checkBoxAll10 == false) {
            this.checkBox10 = []
        }
        emptyArr = _.clone(this.checkBox10)
        this.checkBox10 = _.clone(emptyArr)
    }
    //可选单选
    checkBoxClick10() {
        if (this.checkBox10.length != this.site_list1.length) {
            this.checkBoxAll10 = false;
        } else {
            this.checkBoxAll10 = true;
        }
    }
    // 可选全选
    checkBoxAllClick11() {
        if (this.checkBoxAll11) {
            this.site_list.forEach((item, k) => {
                let valuet = k.toString();
                this.checkBox11.push(valuet);
            });
        } else {
            this.checkBox11 = [];
        }
        this.checkBox11 = _.clone(this.checkBox11);
    }
    //已选单选
    checkBoxClick11() {
        if (this.checkBox11.length === this.site_list.length) {
            this.checkBoxAll11 = true;
        } else {
            this.checkBoxAll11 = false;
        }
    }
    // 右移
    rightFunMovre() {
        let stu = true;
        let temp = [];
        let temp1 = [];
        let arrsright = [];
        this.checkBox10.forEach((item, i) => {
            this.cars_right.forEach(item_2 => {
                if (item_2.surveyQuestionId == this.site_list1[item].surveyQuestionId) {
                    stu = false;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "请不要选择重复属性" });
                    this.checkBox10 = [];
                }
            })
            if (stu) {
                arrsright.push(item);
                arrsright.sort(function (a, b) { return b - a; });
                this.cars_right.push(this.site_list1[item]);
                if (this.site_list.length < 10) {
                    this.site_list.push(this.site_list1[item]);
                }
            }
        });
        if (stu) {
            arrsright.forEach((item, i) => {
                this.site_list1.splice(item, 1)
            });
            this.site_list = _.clone(this.site_list);
            this.cars_right = _.clone(this.cars_right);
            temp1 = _.clone(this.site_list1);
            this.site_list1 = _.clone(temp1);
            this.checkBox10 = [];
            if (this.cars_right) {
                this.total = this.cars_right.length;
            } else {
                this.total = 0;
            }
            // 默认必选组件
            this.comType = [];
            for (let i = 0; i < this.cars_right.length; i++) {
                this.comType.push(i.toString())
            }
        }
        this.checkBoxAll10 = false;
    }
    // 左移
    liftFunMovre() {
        let temp = [];
        let temp1 = [];
        let arrsleft = [];
        this.checkBox11.forEach(item => {
            arrsleft.push(item);
            arrsleft.sort(function (a, b) { return b - a; });
            this.site_list1.push(this.cars_right[item]);
        });
        arrsleft.forEach((item, i) => {
            this.cars_right.forEach((items, k) => {
                if (items.surveyQuestionId === this.site_list[item].surveyQuestionId) {
                    this.cars_right.splice(k, 1);
                }
            });
            this.site_list.splice(item, 1);
        });
        this.cars_right = _.clone(this.cars_right);
        this.site_list = _.clone(this.site_list);
        temp1 = _.clone(this.site_list1);
        this.site_list1 = _.clone(temp1);
        this.checkBox11 = [];
        if (this.cars_right) {
            this.total = this.cars_right.length;
        } else {
            this.total = 0;
        }
        // 默认必选组件
        this.comType = [];
        for (let i = 0; i < this.cars_right.length; i++) {
            this.comType.push(i.toString())
        }
        this.checkBoxAll11 = false;
    }
    // 取消
    addCloseBtn() {
        let parm = {
            display: false,
            code: 'cancelModel'
        };
        this.outValue.emit(parm);
    }
    // 确认
    addSaveBtn() {
        let parm = {
            display: false,
            code: 'cancelModel'
        };
        this.outValue.emit(parm);
    }
    paginate(event) {
        // 每页显示的条数
        this.pageSize = event.rows * 1;
        // 当前页
        let currentPage = event.page + 1;
        let currentPages = currentPage * 10;
        // this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        // 调用查询的方法
        // this.queryClick();
        // this.pageNum = 1;
        if (this.cars_right) {
            this.total = this.cars_right.length;
        } else {
            this.total = 0;
        }
        this.site_list = [];
        if (this.cars_right.length > currentPages) {
            let currentPages2 = currentPages - 10;
            this.site_list = this.cars_right.slice(currentPages2, currentPages);
        } else {
            let currentPages2 = currentPages - 10;
            this.site_list = this.cars_right.slice(currentPages2);
        }
        this.checkBox11 = [];
    }
    paginate1(event) {
        // 每页显示的条数
        this.pageSize1 = event.rows * 1;
        // 当前页
        let currentPage = event.page + 1;
        this.pageNum1 = currentPage;
        this.first1 = (currentPage - 1) * this.pageSize1;
        // 调用查询的方法
        this.queryClick1();
        this.pageNum1 = 1;
    }
}
