import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { BaseInformationBean } from './../bean/base-information.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-base-information-add',
  templateUrl: './base-information-add.component.html',
  styleUrls: ['./../base-information.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 * 分类类型-新增
 */
export class BaseInformationAddComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  // bean
  baseInformationBean: BaseInformationBean = new BaseInformationBean();
  //提示信息
  msgs: Message[] = [];
  userform: FormGroup; //校验
  siteType: any;//站点类型
  code: any;
  iscompele: boolean = false;
  surveyId1: string = 'DC';
  surveyId2: string = '';

  constructor(public productManageHttpService: ProductManageHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc, ) {
    this.userform = fb.group({
      // surveyId: ['', Validators.required],
      surveyId1: ['', Validators.required],
      surveyId2: new FormControl('', [Validators.pattern(/^[0-9]\d*$/), Validators.required]),
      surveyName: ['', Validators.required],
      responseService: [''],
      description: [''],
    });
    this.codeValues(); // 调用方法，获取全部码值
    this.siteType = this.code['SiteType'];
  }

  ngOnInit() {
    this.getSeqNo();
  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }
  // 获取默认编码
  getSeqNo() {
    let parms = {
      seqType: 'SURVEY',
    };
    this.productManageHttpService.getSeqNo(parms).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.surveyId2 = data.seqNo;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  //添加
  addSaveBtn() {
    this.iscompele = true;
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        this.iscompele = false;
        return;
      }
    }
    let surveyId2 = this.surveyId2.split('');
    if (surveyId2.length == 8) {
      this.baseInformationBean.surveyId = this.surveyId1 + this.surveyId2;
      // 提交服务
      this.productManageHttpService.createASurvey(this.baseInformationBean).subscribe(data => {
        if (data.returnCode.code == "success") {
          this.baseInformationBean = new BaseInformationBean();
          let parm = {
            display: false,
            code: data.returnCode.message
          };
          this.outValue.emit(parm);
          this.iscompele = false;
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          this.iscompele = false;
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        this.iscompele = false;
      });
    } else {
      this.iscompele = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "编号必须是DC+8位数字!" });
    }
  }
  // 取消
  addCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    }
    this.outValue.emit(parm);
  }

}

