//  基本信息（调查）-查询 熊正好 2019.4
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ConfirmationService } from 'primeng/primeng';//删除
import { BaseInformationBean } from './../bean/base-information.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'app-base-information-suvery',
    templateUrl: './base-information-suvery.component.html',
    styleUrls: ['./base-information-suvery.component.scss'],
    providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 个性化管理-基本信息（调查）
 */
export class BaseInformationSuveryComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;
    tabMesg: any = tableMessage; // 列表无数据
    // bean
    baseInformationBean: BaseInformationBean = new BaseInformationBean();
    // 修改的参数
    updateValue: any = [];
    // 提示信息
    msgs: Message[] = [];
    // 总页数
    total: string;
    first: number = 0; // 分页控制
    pageSize: number = 10;
    pageNum: any;
    // 模态框
    showModel: string;
    display: boolean = false;
    // 模态框的标题
    headerTitle: string;
    site_list: any = [];
    code: any;

    constructor(public productManageHttpService: ProductManageHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc) {
        this.codeValues(); // 调用方法，获取全部码值
        // this.baseProductType = this.code['BaseProductType'];
    }

    ngOnInit() {
        this.baseInformationBean = this.inValue;
        this.queryClick();
    }
    // 码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret; // code是自己声明的变量
    }
    // 新增调查项
    addClick() {
        this.display = true;
        this.headerTitle = '新增';
        this.showModel = '1';
        this.updateValue = this.baseInformationBean;
    }
    // 新增调查项
    addsCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.first = 0;
        this.queryClick();
    }
    // 配置调查项
    configueClick(item) {
        this.display = true;
        this.headerTitle = '配置';
        this.showModel = '2';
        this.updateValue = this.baseInformationBean;
    }
    // 配置调查项的回调
    configueCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.first = 0;
        this.queryClick();
    }
    //  查询
    queryClick() {
        let pamarm = {
            surveyId: this.baseInformationBean.surveyId,
        };
        this.productManageHttpService.querySurveyProject(pamarm).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.site_list = data.questionList;
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    // 取消
    addCloseBtn() {
        let parm = {
            display: false,
            code: 'cancelModel'
        };
        this.outValue.emit(parm);
    }
    // 确认
    addSaveBtn() {
        let parm = {
            display: false,
            code: 'cancelModel'
        };
        this.outValue.emit(parm);
    }
    // 删除个性化
    deleteClick(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                // 条件
                let param = { baseProductId: item.surveyQuestionId }; // 调查项ID
                this.productManageHttpService.deleteSurveyQuestion(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.first = 0;
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                });
            }
        });
    }
    paginate(event) {
        // 每页显示的条数
        this.pageSize = event.rows * 1;
        // 当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        // 调用查询的方法
        this.queryClick();
        this.pageNum = 1;
    }
}
